import { Button, Container, Grid, Typography } from "@mui/material";
import React from "react";
import { connect } from "react-redux";
import { ImgContribute } from "../../components";
import maintenanceImg from "../../assets/maintenance.gif";
import connectionImg from "../../assets/no_connection.gif";
import { useNavigate } from "react-router-dom";
import { Box } from "@mui/system";
import makeStyles from "@mui/styles/makeStyles";
import { setError } from "../../redux/actions";
const useStyles = makeStyles({
 
  container:{
    alignItems:"center",
    height: window.innerHeight,
    alignContent: "center",
    justifyContent: "center",
    display:"flex"
  }
});
const ExceptionPage = (props) => {
  const { error, actSetError } = props;
  let errImg = "";
  let errText = "";
  // React.useEffect(() => {
  //   actSetError("");
  // }, []);
  switch (error) {
    case "connection":
      errText = "Koneksi anda sedang bermasalah, mohon cek koneksi anda!.";
      errImg = connectionImg;
      break;
    case "not-found":
      errText = "Halaman tidak ada!.";
      errImg = connectionImg;
      break;

    default:
      errText = "Ada Kesalahan, Kita akan coba perbaiki segera!.";
      errImg = maintenanceImg;

      break;
  }
  const classes = useStyles();
  const navigate = useNavigate();
  return (
    <>
      <Box className={classes.container}>
        <Container>
          <Grid container spacing={2}>
            <Grid item xs={12}>
              <ImgContribute
                title="illustrations by Storyset"
                src={errImg}
                alt="Contribute"
                link="https://storyset.com"
              />
            </Grid>
            <Grid item xs={12}>
              <Typography variant="body1" align="center">
                {errText}
              </Typography>
            </Grid>
            <Grid item xs={12}>
              <Button variant="outlined" onClick={() => {
                actSetError("");
                navigate(-1);
              }}>
                Kembali
              </Button>
            </Grid>
          </Grid>
        </Container>
      </Box>
    </>
  );
};

const mapStateToProps = (state) => {
  return {
    error: state.app.error,
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
    actSetError: (body) => {
      dispatch(setError(body))
    }
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(ExceptionPage);
